.frdchk {
  overflow: hidden;
  width: 0;
  height: 0;
  font-size: 0;
  opacity: 0;
  appearance: none;
  -moz-appearance: none;
  /* Firefox */
  -webkit-appearance: none;
  /* Safari and Chrome */ }

.fitem_lab {
  display: inline-block;
  position: relative;
  padding: 2px 0 0 22px;
  min-height: 15px;
  font-size: 12px;
  color: #444444;
