input[type="radio"].frdchk + .fitem_lab:before {
  background-image: url("../images/admin/ico_rd.png") !important; }

.frdchk:checked + .fitem_lab:before {
  background-position: 0 -17px; }

.frdchk:disabled + .fitem_lab:before {
